<template>
  <div class="withdraw" style="padding-top: 44px;">
    <back-header title="立即提现">
      <router-link to="/withdraw/record" class="record">提现记录</router-link>
    </back-header>
    <div class="money">
      <h4>¥{{ info.profit }}</h4>
      <p>账户余额（元）</p>
    </div>
    <div class="input">
      <label>输入提现金额</label>
      <input type="text" v-model="money">
      <span>服务费（费率{{ info.commission }}%）：{{ serviceMoney }}</span>
    </div>
    <div class="account">
      <h4>我的提现账户 <router-link to="/withdraw/account">管理账户</router-link></h4>
      <p>支付宝账号：<span>{{ info.cash_account ? info.cash_account.alipay_account : '' }}</span></p>
      <p>支付宝姓名：<span>{{ info.cash_account ? info.cash_account.alipay_name : '' }}</span></p>
    </div>
    <div class="bottom-btn" @click="withdraw">立即提现</div>
  </div>
</template>

<script>
import BackHeader from '../../components/back-header/back-header'
import { post } from '../../common/js/http'

export default {
  name: 'withdraw',
  components: {
    BackHeader
  },
  data() {
    return {
      info: {
        commission: 1
      },
      money: 0
    }
  },
  created() {
    this.getInfo()
  },
  computed: {
    serviceMoney() {
      return (Number(this.money) * this.info.commission / 100).toFixed(2)
    }
  },
  methods: {
    getInfo() {
      post('/shop/shop/walletInfo')
        .then(({ data: { data } }) => {
          this.info = data
        })
    },
    withdraw() {
      if (this.money < this.info.withdraw_min) {
        this.$toast.fail(`提现金额不能小于${this.info.withdraw_min}`)
        return
      }
      if (!this.info.cash_account) {
        this.$toast.fail('请先设置提现账户')
        return
      }
      const loading = this.$toast.loading()
      post('/shop/shop/withdraw', {
        apply_cash: this.money,
        alipay_account: this.info.cash_account.alipay_account,
        alipay_name: this.info.cash_account.alipay_name
      })
        .then(() => {
          loading.close()
          this.getInfo()
        })
        .catch(() => {
          setTimeout(() => {
            loading.close()
          }, 1000)
          this.getInfo()
        })
    }
  }
}
</script>

